.b5_jqa_pageScroll {
  top: var(--top);
  width: 100%;
  z-index: 1;
  height: var(--page-scroll-height);
  border: 0;
  border-radius: 0;
  position: fixed;
  left: 0;
}

.b5_jqa_pageScroll::-webkit-progress-bar {
  background-color: var(--page-scroll-background-color);
}

.b5_jqa_pageScroll::-webkit-progress-value {
  background-color: var(--page-scroll-bar-color);
}

.b5_jqa_pageScroll::-moz-progress-bar {
  background-color: var(--page-scroll-bar-color);
}

